'use client';

import React, { useState, useEffect } from 'react';
import {
  Nav,
  NavbarBrand,
  NavbarToggler,
  NavItem,
  NavLink,
  Container,
  Collapse,
  Button,
} from 'reactstrap';
import Image from 'next/image';
import { config } from '@/config';

const NavItems = [
    { id: 1, idnm: "home", navheading: "Inicio" },
    { id: 2, idnm: "features", navheading: "Resumen" },
    { id: 3, idnm: "services", navheading: "Habilidades" },
    { id: 4, idnm: "about", navheading: "Sobre mí" },
    { id: 5, idnm: "testi", navheading: "Proyectos" },
    { id: 6, idnm: "blog", navheading: "Certificados" },
    { id: 7, idnm: "contact", navheading: "Contacto" },
];

export default function Navbar({ navClass }: { navClass?: string }) {
    const [isOpenMenu, setIsOpenMenu] = useState(false);
    const [sticky, setSticky] = useState(false);
    const [activeSection, setActiveSection] = useState("home");

    const toggle = () => setIsOpenMenu(!isOpenMenu);

    useEffect(() => {
        const handleScroll = () => {
             if (window.scrollY >= 50) {
                setSticky(true);
             } else {
                setSticky(false);
             }

             // Lógica simplificada de ScrollSpy
             const scrollPosition = window.scrollY + 100; // Offset para detectar la sección
             
             for (const item of NavItems) {
                 const section = document.getElementById(item.idnm);
                 if (section && section.offsetTop <= scrollPosition && (section.offsetTop + section.offsetHeight) > scrollPosition) {
                     setActiveSection(item.idnm);
                 }
             }
        };

        window.addEventListener("scroll", handleScroll);
        return () => window.removeEventListener("scroll", handleScroll);
    }, []);

    const openResume = () => {
        window.open(config.urlHojaVida, "_blank");
    };

    return (
        <div className={`navbar navbar-expand-lg fixed-top navbar-custom sticky sticky-dark ${sticky ? "nav-sticky" : ""} ${navClass || ""}`} id="navbar">
            <Container>
                <NavbarBrand className="logo text-uppercase" href="/">
                    <Image 
                        src="/assets/images/NuevoLogo.png" 
                        alt="Logo" 
                        width={50} 
                        height={50} 
                        style={{objectFit: "contain"}} 
                        priority 
                    />
                </NavbarBrand>

                <NavbarToggler onClick={toggle}>
                    <i className="mdi mdi-menu"></i>
                </NavbarToggler>

                <Collapse isOpen={isOpenMenu} navbar id="navbarCollapse">
                    <Nav className="navbar-nav navbar-center" id="mySidenav" navbar>
                        {NavItems.map((item, key) => (
                            <NavItem key={key} className={activeSection === item.idnm ? "active" : ""}>
                                <NavLink href={`#${item.idnm}`}>
                                    {item.navheading}
                                </NavLink>
                            </NavItem>
                        ))}
                    </Nav>
                    <div className="nav-button ms-auto">
                        <Nav className="navbar-right nav" navbar>
                            <NavItem>
                                <Button
                                    type="button"
                                    color="primary"
                                    style={{ color: "white" }}
                                    className="navbar-btn btn-rounded waves-effect waves-light"
                                    onClick={openResume}
                                >
                                    Mira HV
                                </Button>
                            </NavItem>
                        </Nav>
                    </div>
                </Collapse>
            </Container>
        </div>
    );
}
